<template lang="html">
<div class="modal-body">
  <div class="modal-close"
       @click="$emit('modal-hide')">+</div>

  <form v-show="addContactShowBool">
    <div class="contact-image">
      <img src="../assets/no-avatar.jpg"/>
    </div>
    <div class="modal-edit-inputs">
      <div class="modal-input">
        <input type="text"
               v-model="nameInput">
      </div>
      <div class="modal-input">
        <input type="text"
               v-model="phoneInput">
      </div>
      <div class="modal-input">
        <input type="submit"
               @click="$emit('add-contact', [nameInput, phoneInput]), clearInputs(), $emit('modal-hide')"
               @click.prevent.self>
      </div>
    </div>
  </form>

  <form v-show="editContactShowBool">
    <div class="contact-image">
      <img src="../assets/no-avatar.jpg"/>
    </div>
    <div class="modal-edit-inputs">
      <div class="modal-input">
        <input type="text"
               v-model="nameInput">
      </div>
      <div class="modal-input">
        <input type="text"
               v-model="phoneInput">
      </div>
      <div class="modal-input">
        <input type="submit"
               @click="$emit('edit-contact', [nameInput, phoneInput, indexOfEdit]), clearInputs(), $emit('modal-hide')"
               @click.prevent.self>
      </div>
    </div>
  </form>
</div>
</template>

<script>
export default {
  name: 'Modal',
  data() {
    return {
      nameInput: '',
      phoneInput: '',
      addContactShowBool: false,
      editContactShowBool: false,
      indexOfEdit: ''
    }
  },
  methods: {
    clearInputs() {
      this.nameInput = '';
      this.phoneInput = '';
    },

    addContactShow() {
      this.addContactShowBool = true;
      this.editContactShowBool = false;
    },

    editContactShow(contactInfoObject, index) {
      this.editContactShowBool = true;
      this.addContactShowBool = false;

      this.indexOfEdit = index;

      this.nameInput = contactInfoObject.name;
      this.phoneInput = contactInfoObject.number;
    },

    hideModals() {
      this.addContactShowBool = false;
      this.editContactShowBool = false;
    }
  }
}
</script>

<style lang="scss">
  .modal-body {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 500px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 0 20px rgba(#000, .5);

    .modal-edit-inputs {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .modal-input {
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .modal-close {
      position: absolute;
      top: 15px;
      right: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 20px;
      height: 20px;
      background-color: #fff;
      cursor: pointer;
      border: 1px solid #ccc;
      border-radius: 50%;
      font-size: 20px;
      font-weight: 700;
      transform: rotate(45deg);
    }
  }
</style>



